import React from "react";
import { CourseButtonProps } from "../interfaces/projectInterfaces";
import { DropdownMenu } from "./DropdownMenu";

export function CourseDropdownMenu({
    horizontal,
    select,
    edit,
    del,
    move
}: CourseButtonProps): JSX.Element {
    return (
        <DropdownMenu
            horizontal={horizontal != undefined ? horizontal : true}
            buttons={[
                {
                    variant: "primary",
                    text: "Select",
                    click: select
                },
                {
                    text: "Edit",
                    click: edit
                },
                {
                    variant: "danger",
                    text: "⦻",
                    click: del
                },
                {
                    variant: "secondary",
                    text: "Move",
                    click: move
                }
            ]}
        />
    );
}
